<template>
  <el-container style="height: 900px; border: 1px solid #eee">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-button class="button-firefox-ui" v-on:click="firefoxUI" type="primary" plain :disabled="isExec">
        FireFox UI 执行
      </el-button>

      <el-button class="button-chrome-ui" v-on:click="chromeUI" type="primary" plain :disabled="isExec">
        Chrome UI 执行
      </el-button>

      <el-button class="button-api" v-on:click="interfaceTask" type="primary" plain :disabled="isExec">
        interface执行
      </el-button>

      <el-button class="button-status" v-on:click="checkStatus" type="primary" plain :disabled="isExec">
        执行状态查询
      </el-button>

      <el-input placeholder="输入browser" v-model="browser"></el-input>
      <el-input placeholder="输入tenant" v-model="tenant"></el-input>
      <el-input placeholder="输入db类型" v-model="db"></el-input>
      <el-input placeholder="是否Saas" v-model="isSaas"></el-input>
      <el-input placeholder="report url" v-model="showReportValue"></el-input>

    </el-aside>

    <el-container>
      <el-header>
        <el-input class="input-message" type="text" disabled="disabled" readonly="true" :value="showValue" ></el-input>
      </el-header>

      <el-main>
        <iframe class="iframe-report" :src="showReport"></iframe>
      </el-main>
    </el-container>
  </el-container>


</template>

<script>

import axios from "axios";

export default {
  data() {
    return {
      showValue:'状态显示',
      showReport:'',
      showReportValue:null,
      browser:null,
      db:'mysql',
      tenant:'t01',
      isSaas:false,
      isExec:false
    }
  },
  methods: {
    firefoxUI(){
      let params = {
        browser:null,
        db:this.db,
        tenant:this.tenant,
        isSaas:this.isSaas}
      axios.get('/ui_automation/start',{params:params}).then(
          (resp) => {
            this.showValue=resp.data
          }
      );
    },
    chromeUI(){
      let params = {
        browser:'chrome',
        db:this.db,
        tenant:this.tenant,
        isSaas:this.isSaas}
      axios.get('/ui_automation/start',{params:params}).then(
          (resp) => {
            this.showValue=resp.data
          }
      );
    },
    interfaceTask(){
      let params = {
        db:this.db,
        tenant:this.tenant,
        isSaas:this.isSaas}
      axios.get('/interface_automation/start',{params:params}).then(
          (resp) => {
            this.showValue=resp.data
          }
      );
  },
    checkStatus(){
      axios.get('/report/check').then(
          (resp) => {
            this.showValue=resp.data
            this.isExec=resp.data
            if(!this.isExec){
              this.showReport=this.showReportValue;
            }
          }
      );
    },
    getReportUrl(){
      axios.get('/report/url').then(
          (resp) => {
            if(!this.isExec){
              this.showReportValue=resp.data;
              this.showReport=this.showReportValue;
            }
          }
      );
    }
  },
  watch: {},
  mounted() {
    this.checkStatus();
    this.getReportUrl();
  }
}

</script>

<style scoped lang="scss">
.el-header {
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}

.iframe-report {
  width: 98%;
  height: 98%;
}

.el-button {
  margin-top: 20px;
  margin-left: 5px;
}

.el-aside {
  .el-input {
    margin-top: 20px;
  }
}

.input-message {
  font-size: large;
}

</style>
