import NotFound from '../components/404.vue'
import Tasks from "../components/Tasks";

let routes = [
    {
        path: '/',
        redirect: '/Tasks'
    },
    {
        path: '/Tasks',
        component: Tasks,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '*',
        hidden: true,
        redirect: {path: '/404'}
    }
];

export default routes;